header {
  background-color: #333;
  padding: 20px;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  border-bottom: 2px solid #444;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 12px;
  flex: 1;
}

.nav-links li {
  display: inline;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 4px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.nav-links a:hover {
  background-color: #00b4d8;
  color: #333;
}

.header-title {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.5rem;
  font-weight: bold;
  color: #00b4d8;
  margin: 0;
  text-align: center;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
  .header-title {
    font-size: 2rem;
  }

  .nav-links {
    gap: 10px;
  }

  .nav-links a {
    padding: 5px;
  }
}

@media (max-width: 480px) {
  header {
    padding: 15px;
  }

  .nav-links {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .nav-links a {
    display: block;
  }
}